<template>
  <div class="colockTable">
    <!--查询信息的嵌套表单开始--->
    <el-dialog :visible.sync="this.dialogCancelFormVisible" title="查询学生信息">
      <el-form :model="stuForm">
        <el-form-item :label-width="this.formLabelWidth" label="学号">
          <el-input v-model="stuForm.studentNum" autocomplete="off"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogCancelFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="searchTableData">确 定</el-button>
      </div>
    </el-dialog>
    <!--查询信息的嵌套表单结束--->
    <div class="btnContiner">
      <div>
        <download-excel :data="students" :fields="json_fields"><button class="button">导出成表格</button></download-excel>
      </div>
      <div>
        <button class="button" type="button" @click="alertWin">通知</button>
      </div>
      <div>
        <button class="button" type="button" @click="dialogCancelFormVisible = true">查询</button>
      </div>
    </div>
    <el-table
        :data="students"
        stripe
        style="width: 100%"
        class="studentColockTable">
      <el-table-column
          prop="index"
          label="序号"
          width="55"/>
      <el-table-column
          prop="date"
          label="日期"
          width="150"/>
      <el-table-column
          prop="studentNum"
          label="学号"
          width="150"/>
      <el-table-column
          prop="studentName"
          label="姓名"
          width="100"/>
      <el-table-column
          prop="college"
          label="学院"/>
      <el-table-column
          prop="reason"
          label="健康状况"/>
    </el-table>
  </div>
</template>
<script>
// 获取当前时间戳
//  var date=Date.parse(new Date())
// 若当前数据是个位数啧添加个0
function add0(m){
  return m<10?'0'+m:m
  }
export default {
  data () {
    return {
      formLabelWidth: '120px',
      stuForm:{
        index: null,
        date: '',
        studentNum: null,
        studentName: '',
        college: ''
      },
      // 获取的学生数据
      students: [{
        index: 1,
        date: '2020-11-13',
        studentNum: 201805090217,
        studentName: '杨晓虎',
        college: '地球科学与技术学院',
        reason:'良好'
      }, {
        index: 2,
        date: '2020-11-13',
        studentNum: 201805090218,
        studentName: '彭晓松',
        college: '地球科学与技术学院',
        reason:'良好'
      }, {
        index: 3,
        date: '2020-11-13',
        studentNum: 201805090219,
        studentName: '许晓豪',
        college: '地球科学与技术学院',
        reason:'良好'
      }, {
        index: 4,
        date: '2020-11-13',
        studentNum: 201805090220,
        studentName: '王晓迪',
        college: '地球科学与技术学院',
        reason:'良好'
      }, {
        index: 5,
        date: '2020-11-13',
        studentNum: 201805090221,
        studentName: '李晓飞',
        college: '地球科学与技术学院',
        reason:'良好'
      }],
      dialogAddFormVisible:false,
      dialogCancelFormVisible:false,
      // 导出表格时所需要配置的表头字段
      json_fields: {
        '序号': 'index',
        '日期': 'date',
        '学号': 'studentNum',
        '姓名': 'studentName',
        '学院': 'college'
      }
    }

  },
  methods:{
    // 将时间戳变成日期格式
    format(shijianchuo){
    // shijianchuo是整数，否则要parseInt转换
      var time = new Date(shijianchuo);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      var h = time.getHours();
      var mm = time.getMinutes();
      var s = time.getSeconds();
      return y+'-'+add0(m)+'-'+add0(d)+' '+add0(h)+':'+add0(mm)+':'+add0(s);
    },
    alertWin:function(){
      alert('已向未打卡的学生发送通知')
    },
    addTableData:function(){
      let arr=this.students.pop()
      this.students.push(arr)
      let index=arr.index+1
      console.log(index)
      let obj={
        index:index,
        date:this.stuForm.date,
        studentNum:this.stuForm.studentNum,
        studentName:this.stuForm.studentName,
        college:this.stuForm.college
        }
      this.students.push(obj)
      this.dialogAddFormVisible=false
      console.log(this.dialogAddFormVisible)
      this.stuForm.studentNum=null
  },
  searchTableData:function(){
      let index
      // this.students.forEach((item,i)=>item.studentNum===this.stuForm.studentNum?index=i:index=null)
      for (let i=0;i<this.students.length;i++) {
        if(this.stuForm.studentNum==this.students[i].studentNum)
        {
          index=i
          console.log(index)
        }
      }
      let temp=this.students[index]
      this.students=[]
      this.students.push(temp)
      console.log(index)
      this.dialogCancelFormVisible=false
    }
  }
}
</script>
<style scoped>
*{
margin: 0;
padding: 0;
}
.studentColockTable{
    justify-content: center;
    margin:10px auto;
}
.button{
    height: 40px;
    width: 100px;
    background-color: #5800b0;
    border: none; color: white;
    padding: 2px 2px;
    margin: 10px 0;
    border-radius: 30px;
    text-align: center;
    text-decoration: none;
    display:inline-block;
    font-size: 12px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s;/* Safari */
    transition-duration:0.4s;
    }
.button:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19)
  }
.btnContiner {
  height: 50px;
  width: 400px;
  display: flex;
  align-content: center;
  justify-content: center;
  }

</style>



