<template>
  <div>
    <el-dialog :visible.sync="this.dialogAddFormVisible" title="添加预约选项">
      <el-form :model="stuForm">
        <el-form-item :label-width="this.formLabelWidth" label="选项名称">
          <el-input v-model="stuForm.studentNum" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label-width="this.formLabelWidth" label="选型内容">
          <el-input v-model="stuForm.studentName" autocomplete="off"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addConfirm">确 定</el-button>
      </div>
    </el-dialog>
    <!--添加信息的嵌套表单结束--->
    <!--删除信息的嵌套表单开始--->
    <el-dialog :visible.sync="this.dialogCancelFormVisible" title="预约选项">
      <el-form :model="stuForm">
        <el-form-item :label-width="this.formLabelWidth" label="选项名称">
          <el-input v-model="stuForm.studentNum" autocomplete="off"/>
        </el-form-item>
        <el-form-item :label-width="this.formLabelWidth" label="选项内容">
          <el-input v-model="stuForm.studentName" autocomplete="off"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogCancelFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="cancelConfirm">确 定</el-button>
      </div>
    </el-dialog>
    <!--删除信息的嵌套表单结束--->
    <div class="btnContiner">
      <div>
        <download-excel :data="tableData" :fields="json_fields"><button class="button">导出成表格</button></download-excel>
      </div>
      <div>
        <button class="button" type="button" @click="dialogAddFormVisible = true">添加</button>
      </div>
      <div>
        <button class="button" type="button" @click="dialogCancelFormVisible = true">删除</button>
      </div>
    </div>
    <el-table
        :data="tableData"
        stripe
        style="width: 100%"
        class="studentOrderLeave">
      <el-table-column
          prop="index"
          label="序号"
          width="55"/>
      <el-table-column
          prop="studentNum"
          label="学号"
          width="155"/>
      <el-table-column
          prop="studentName"
          label="姓名"
          width="155"/>
      <el-table-column
          prop="orderContent"
          label="预约内容"
          width="155"/>
      <el-table-column
          prop="orderPlace"
          label="预约地点"
          width="155"/>
      <el-table-column
          prop="orderTime"
          label="预约时间"
          width="155"/>
      <el-table-column
          prop="college"
          label="学院"
          width="155"/>
    </el-table>
  </div>
</template>
<script>
// 获取当前时间戳
//  var date=Date.parse(new Date())
// 若当前数据是个位数啧添加个0
function add0(m){
  return m<10?'0'+m:m
  }
export default {
  data () {
    return {
      dialogAddFormVisible:false,
      dialogCancelFormVisible:false,
      formLabelWidth: '120px',
      stuForm:{
        index: null,
        studentNum: null,
        studentName: '',
        orderContent: '',
        orderPlace: '',
        orderTime: '',
        college: ''
      },
      myDate: Date,
      backDate: function backDateCalculate () {
        var backDay = new Date()
        var backDate = backDay.getDate() + Math.floor(Math.random() * (5 - 3)) + 3
        return backDay.getFullYear() + '-' + backDay.getMonth() + '-' + backDate
      },
      tableData: [{
        index: 1,
        studentNum: 201805090217,
        studentName: '李红绸',
        orderContent: '洗澡预约',
        orderPlace: '荷园',
        orderTime: '8:00-10:00',
        college: '地球科学与技术学院'
      }, {
        index: 2,
        studentNum: 201805090218,
        studentName: '邵壮壮',
        orderContent: '洗澡预约',
        orderPlace: '荷园',
        orderTime: '8:00-10:00',
        college: '地球科学与技术学院'
      }, {
        index: 3,
        studentNum: 201805090219,
        studentName: '吴凡达',
        orderContent: '洗澡预约',
        orderPlace: '荷园',
        orderTime: '8:00-10:00',
        college: '地球科学与技术学院'
      }, {
        index: 4,
        studentNum: 201805090220,
        studentName: '武光',
        orderContent: '洗澡预约',
        orderPlace: '荷园',
        orderTime: '8:00-10:00',
        college: '地球科学与技术学院'
      }, {
        index: 5,
        studentNum: 201805090221,
        studentName: '王格一',
        orderContent: '洗澡预约',
        orderPlace: '荷园',
        orderTime: '8:00-10:00',
        college: '地球科学与技术学院'
      }],
      json_fields: {
        '序号': 'index',
        '学号': 'studentNum',
        '姓名': 'studentName',
        '预约内容': 'orderContent',
        '预约地点': 'orderPlace',
        '预约时间':'orderTime',
        '学院':'college'
      }
    }
  },
  methods: {
   // 将时间戳变成日期格式
    format(shijianchuo){
    // shijianchuo是整数，否则要parseInt转换
    var time = new Date(shijianchuo);
    var y = time.getFullYear();
    var m = time.getMonth()+1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return y+'-'+add0(m)+'-'+add0(d)+' '+add0(h)+':'+add0(mm)+':'+add0(s);
    },
    addTableData:function(){
    let arr=this.tableData.pop()
    this.tableData.push(arr)
    let index=arr.index+1
    console.log(index)
    let obj={
      index: index,
      studentNum: this.stuForm.studentNum,
      studentName: this.stuForm.studentName,
      orderContent: this.stuForm.orderContent,
      orderPlace: this.stuForm.orderPlace,
      orderTime: this.stuForm.orderTime,
      college: this.stuForm.college
      }
    this.tableData.push(obj)
    this.dialogAddFormVisible=false
    console.log(this.dialogAddFormVisible)
    this.stuForm.studentNum=null
  },
  cancelTableData:function(){
    let index
    this.tableData.forEach((item,i)=>item.studentNum===this.stuForm.studentNum?index=i:index=null)
    this.tableData.splice(index,1)
    this.dialogCancelFormVisible=false
    },
    addConfirm:function(){
      this.dialogAddFormVisible=false
      alert('新的预约规则：'+this.stuForm.studentNum+'-----'+this.stuForm.studentName
      +'已被添加进表单')
    },
    cancelConfirm:function(){
      this.dialogCancelFormVisible=false
      alert('预约规则：'+this.stuForm.studentNum+'-----'+this.stuForm.studentName
      +'已被从表单删除')
    }
  }
}
</script>
<style scoped>
    *{
        margin:0;
        padding: 0;
    }
    .studentOrderLeave{
        justify-content: center;
        margin:0 auto;
    }
    .button{
    background-color: #5800b0;
    border: none; color: white;
    padding: 2px 2px;
    margin: 10px 0;
    text-align: center;
    text-decoration: none;
    display:inline-block;
    font-size: 12px;
    margin: 4px 2px;
    cursor: pointer;
    -webkit-transition-duration: 0.4s;/* Safari */
    transition-duration:0.4s;
    border-radius: 30px;
    }
    .button:hover {
     box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19)
    }
</style>



