<template>
  <div>
    <div class="pie">
      <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
      <div id="notClock" />
    </div>
  </div>
</template>

<script>// 引入基本模板
import echarts from 'echarts'
var college=['水利科学与工程学院','地球科学与技术学院','医学院','生态与环境学院','机械学院']
var numNotClock=[
  {value:133,name:'水利科学与工程学院'},
  {value:172,name:'地球科学与技术学院'},
  {value:203,name:'医学院'},
  {value:237,name:'生态与环境学院'},
  {value:261,name:'机械学院'}
  ]
// 中介数组，把结果赋值给最终的数组
var temp=[]
// 初始化数据
function initData(){
  for(let i=0;i<college.length;i++){
    let randowNum=Math.floor(Math.random() * (50 - 250 + 1) + 250); // 获取随机数，范围0--8
    let obj={
    value:randowNum,
    name:college[i]
    }
    temp.push(obj)
  }
    numNotClock=temp
    temp=[]
    return numNotClock
}
export default {
  data(){
    return{
      echartsOption:{
        title : {
              text: '当前未健康打卡人数',// 主标题
              subtext: '',// 副标题
              x:'center',// x轴方向对齐方式
          },
          tooltip : {
              trigger: 'item',
              formatter: '{a} <br/>{b} : {c} ({d}%)'
          },
          legend: {
              orient: 'vertical',
              bottom: 'bottom',
              data: college
          },
          series : [
              {
                  name: '未打卡人数',
                  type: 'pie',
                  radius : '55%',
                  center: ['50%', '60%'],
                  data:numNotClock,
                  itemStyle: {
                      emphasis: {
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }
          ]
      }
    }
  },
  mounted(){
    this.initChart()
    setInterval(this.updateData,1000)// 1s更新一次数据
  },
  methods:{
    // 初始化数据
    initChart() {
      // 基于准备好的dom，初始化echarts实例
      this.myChart = echarts.init(document.getElementById('notClock'))
      // 绘制图表
      this.myChart.setOption(this.echartsOption)
    },
    updateData:function(){
      numNotClock.forEach((item,i)=>{
        if(item.value>0)
        {
          item.value-=Math.floor(Math.random() * (0 - 10 + 1) + 10)
        }
        else if(item.value<0){
          item.value=0
        }
        })
      this.myChart.setOption(this.echartsOption)
    }
  }
}
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  #notClock{
    float:left;
    width:100%;
    height: 300px
  }
</style>


