<template>
  <div>
    <div class="searchInput">
      <div>
        <el-input
            v-model="searchName"
            type="text"
            autocomplete="on"
            autocorrect="off"
            autocapitalize="off"
            spellcheck="false"
            placeholder="搜索..."/>
      </div>
      <div>
        <el-button class="searchBtn " @click="searchTrackSet">查询</el-button>
      </div>
      <div>
        <el-button class="searchBtn" type="button" @click="mapTrackCancelClick">清除轨迹</el-button>
      </div>
      <div>
        <el-button class="mapMarkerCancel" type="button" @click="mapStorageCancelClick">清除缓存</el-button>
      </div>
    </div>
    <div id="mapContainer"/>
  </div>
</template>
<script>
import AMap from 'AMap'
import storage from 'good-storage'
var heatmap
var polyLine={}
var marker={}
var circle={}
export default {
  data () {
    return {
      searchName:'',
      map: {},
      gisLayer: {},
      studentPosition: [{longitude:113.670326,latitude:34.766054}],
      illPersonAddress: [
        {lng: 113.711138, lat: 34.435657, count: 10},
        {lng: 113.698192, lat: 34.780414, count: 20},
        {lng: 113.63998, lat: 34.72465, count: 30},
        {lng: 113.640372, lat: 34.799702, count: 40},
        {lng: 113.616601, lat: 34.75598, count: 50},
        {lng: 113.677461, lat: 34.795344, count: 60},
        {lng: 113.656041, lat: 34.707536, count: 70},
        {lng: 113.653561, lat: 34.732948, count: 80},
        {lng: 113.622004, lat: 34.745496, count: 90},
        {lng: 113.7171, lat: 34.727875, count: 100},
        {lng: 113.650802, lat: 34.812271, count: 110},
        {lng: 113.54122, lat: 34.908063, count: 120},
        {lng: 113.648397, lat: 34.790022, count: 130},
        {lng: 113.64609, lat: 34.752743, count: 140}
      ],
      geoFence:[{longitude:113.670326,latitude:34.766054},
                {longitude:113.658789,latitude:34.769543},
                {longitude:113.710187,latitude:34.612102},
                {longitude:113.64609, latitude:34.752743},
                {longitude:113.61224, latitude:34.750299},
                {longitude:113.684496,latitude:34.749867}],
      riskGrade:[
                {longitude:114.7111 , latitude: 34.13278},
                {longitude:113.698192 , latitude: 34.780414},
                {longitude:113.616601 , latitude: 34.75598},
                {longitude:113.656041 , latitude: 34.707536},
                {longitude:113.622004 , latitude: 34.745496},
                {longitude:113.650802 , latitude: 34.812271},
                {longitude:113.648397 , latitude: 34.790022},
                {longitude:113.711138 , latitude: 34.435657},
                {longitude:113.03015 , latitude: 34.626022},
                {longitude:113.677461 , latitude: 34.795344},
                {longitude:113.653561 , latitude: 34.732948},
                {longitude:113.7171 , latitude: 34.727875},
                {longitude:113.54122 , latitude: 34.908063},
                {longitude:113.64609 , latitude: 34.752743},
                {longitude:113.640372 , latitude: 34.799702}],
      studentTracks: [
                  {track: [
                  {longitude: 113.541482, latitude: 34.817038},
                  {longitude: 113.543408, latitude: 34.817007},
                  {longitude: 113.545114, latitude: 34.817051},
                  {longitude: 113.547914, latitude: 34.817073},
                  {longitude: 113.549872, latitude: 34.817082},
                  {longitude: 113.551411, latitude: 34.817082},
                  {longitude: 113.55146, latitude: 34.818527},
                  {longitude: 113.551444, latitude: 34.820434},
                  {longitude: 113.551411, latitude: 34.821979},
                  {longitude: 113.552007, latitude: 34.821984},
                  {longitude: 113.552554, latitude: 34.822094},
                  {longitude: 113.554008, latitude: 34.82209},
                  {longitude: 113.556089, latitude: 34.82209},
                  {longitude: 113.557275, latitude: 34.822112},
                  {longitude: 113.55728, latitude: 34.823578}],
                                                name: '杨晓虎'},
                {track: [
                  {longitude: 113.535533, latitude: 34.808133},
                  {longitude: 113.535533, latitude: 34.807851},
                  {longitude: 113.535533, latitude: 34.807578},
                  {longitude: 113.536531, latitude: 34.807569},
                  {longitude: 113.536563, latitude: 34.806617},
                  {longitude: 113.536595, latitude: 34.805314},
                  {longitude: 113.536573, latitude: 34.804133},
                  {longitude: 113.536595, latitude: 34.802785},
                  {longitude: 113.536595, latitude: 34.801728},
                  {longitude: 113.538, latitude: 34.801402},
                  {longitude: 113.539792, latitude: 34.801385},
                  {longitude: 113.540554, latitude: 34.801402},
                  {longitude: 113.540683, latitude: 34.800918},
                  {longitude: 113.540768, latitude: 34.798883},
                  {longitude: 113.540779, latitude: 34.79764},
                  {longitude: 113.540758, latitude: 34.795385},
                  {longitude: 113.540801, latitude: 34.792618}],
                                                name: '彭晓松'},
                {track: [
                  {longitude: 113.541493, latitude: 34.817047},
                  {longitude: 113.542008, latitude: 34.817038},
                  {longitude: 113.541943, latitude: 34.816298},
                  {longitude: 113.541884, latitude: 34.815299},
                  {longitude: 113.541874, latitude: 34.814528},
                  {longitude: 113.541852, latitude: 34.814277},
                  {longitude: 113.541755, latitude: 34.812903},
                  {longitude: 113.541702, latitude: 34.811533},
                  {longitude: 113.541654, latitude: 34.809617},
                  {longitude: 113.541573, latitude: 34.807886},
                  {longitude: 113.543649, latitude: 34.807802},
                  {longitude: 113.54631, latitude: 34.807794},
                  {longitude: 113.548499, latitude: 34.807789},
                  {longitude: 113.548488, latitude: 34.807106},
                  {longitude: 113.548107, latitude: 34.80656},
                  {longitude: 113.548944, latitude: 34.806485},
                  {longitude: 113.549818, latitude: 34.806349},
                  {longitude: 113.54977, latitude: 34.805591}],
                                                name: '王晓迪'}],
      TrackLngLat1:[],
      TrackLngLat2:[],
      TrackLngLat3:[],
      TrackLngLat:[]
    }
  },
  mounted: function () {
    this.init()
  },
  methods: {
    init: function () {
      const epcMap = new AMap.Map('mapContainer', {
        resizeEnable: true,
        zoom: 14,
        center: [113.53639, 34.81701]
      })
      epcMap.plugin(['AMap.ToolBar', 'AMap.Scale'], function () {
        epcMap.addControl(new AMap.ToolBar())
        epcMap.addControl(new AMap.Scale())
      })
      // console.log(this.studentPosition.length)
      epcMap.on('click', this.mapClick)
      this.map = epcMap
      storage.set('illPersonAddress', this.illPersonAddress)
    },
    // 清除缓存的按钮事件
    mapStorageCancelClick: function () {
      storage.clear()
      this.$showLoading()
      setTimeout(this.hide,1000)
    },
    // 清除轨迹的按钮事件
    mapTrackCancelClick:function(){
      this.$showLoading()
      setTimeout(this.hide,1000)
    },
    // 当完成任务的时候就关闭loading
    hide() {
      if(polyLine){
        this.map.remove(polyLine)
        this.TrackLngLat.length=0
      }
      alert('清除完成')
      this.$hideLoading()
    },
    //
    //
    // -----------------------------------------------------------------------------------
    // 地图点击事件
    // -----------------------------------------------------------------------------------
    mapClick: function (e) {
      /**
       * 创建点标记对象
       */
      var personMarker = new AMap.Marker({
        position: new AMap.LngLat(e.lnglat.getLng(), e.lnglat.getLat()) // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
      })
      this.map.add(personMarker)
      /**
       * 将获取到的点存储进位置数组
       */
      var ePos = {
        longitude: e.lnglat.getLng(),
        latitude: e.lnglat.getLat()
      }
      // console.log(ePos)
      this.studentPosition.push(ePos)
      this.studentTrack2.push(ePos)
      storage.set('studentPosition', this.studentPosition)
      // console.log(this.studentPosition)
    },
    // -----------------------------------------------------------------------------------
    // 轨迹对象创建
    // -----------------------------------------------------------------------------------
    addTrack1: function(item,index){
      this.TrackLngLat1.push (new AMap.LngLat(item.longitude,item.latitude))
    },
    addTrack2: function(item,index){
      this.TrackLngLat2.push (new AMap.LngLat(item.longitude,item.latitude))
    },
    addTrack3: function(item,index){
      this.TrackLngLat3.push (new AMap.LngLat(item.longitude,item.latitude))
    },
    addRiskGradeCircle: function(item,index){
      circle = new AMap.Circle({
              center: new AMap.LngLat(item.longitude,item.latitude), // 圆心位置
              radius: 200, // 圆半径
              fillColor: 'red', // 圆形填充颜色
              strokeColor: 'green', // 描边颜色
              strokeWeight: 2, // 描边宽度
      })
      this.map.add(circle)
    },
    // 添加地理围栏
    addGeoFence:function(item,index){
      circle = new AMap.Circle({
              center: new AMap.LngLat(item.longitude,item.latitude), // 圆心位置
              radius: 200, // 圆半径
              fillColor: 'red', // 圆形填充颜色
              strokeColor: 'green', // 描边颜色
              strokeWeight: 2, // 描边宽度
              fillOpacity:0.2// 覆盖物透明度
      })
      this.map.add(circle)
    },
    // -----------------------------------------------------------------------------------
    // 轨迹对象加载到地图上
    // -----------------------------------------------------------------------------------
    searchTrackSet: function(){
      if(this.$store.state.trackSearchBool){
      console.log(this.searchName)
      let index
      // this.studentTracks.forEach((item,i)=>{item.name===this.searchName?index=i:index=null})
      for(let i=0;i<this.studentTracks.length;i++){
        if(this.studentTracks[i].name===this.searchName)
        {
          index=i
        }
      }
      if(polyLine)
      {
        this.map.remove(polyLine)
        this.TrackLngLat.length=0
      }
      console.log(index)
      switch(index){
        case 0:this.studentTracks[0].track.forEach((item,index)=>this.addTrack1(item,index))
        this.TrackLngLat=this.TrackLngLat1
        break;
        case 1:this.studentTracks[1].track.forEach((item,index)=>this.addTrack2(item,index))
        this.TrackLngLat=this.TrackLngLat2
        break;
        case 2:this.studentTracks[2].track.forEach((item,index)=>this.addTrack3(item,index))
        this.TrackLngLat=this.TrackLngLat3
        break;
        default:
        break
      }
       polyLine = new AMap.Polyline({
                  path: this.TrackLngLat,
                  borderWeight: 2, // 线条宽度，默认为 1
                  strokeColor: 'red', // 线条颜色
                  lineJoin: 'round' // 折线拐点连接处样式
                   })
                  this.map.add(polyLine)
      }
      else{
        alert('尚未激活轨迹查询功能，请再次单击轨迹查询按钮来激活此功能')
      }
    }

  },
  computed: {
    // 监听全局变量mapPosition 并返回其值
    listenMapPosition () {
      console.log(this.$store.state.mapPosition)
      return this.$store.state.mapPosition
    },
    // 监听全局变量trackSearchBool 并返回其值
    listenTrackSearchBool () {
      console.log(this.$store.state.trackSearchBool)
      return this.$store.state.trackSearchBool
    },
    // 监听全局变量riskGradeBool 并返回其值
    listenRiskGradeBool () {
      console.log(this.$store.state.riskGradeBool)
      return this.$store.state.riskGradeBool
    },
    // 监听全局变量geoFenceBool 并返回其值
    listenGeoFenceBool () {
      console.log(this.$store.state.geoFenceBool)
      return this.$store.state.geoFenceBool
    }
  },
  watch: {
    // 当为true的时候就是被点击了，同时把其他的监听的全局变量变成false
    // 显示位置功能的实现
    listenMapPosition: function (val, oldVal) {
      if(val===true){
      this.$store.state.trackSearchBool=false
      this.$store.state.riskGradeBool =false
      this.$store.state.geoFenceBool =false
      this.studentPosition = storage.get('studentPosition') || [{longitude: 113.711138, latitude: 34.435657}]
      console.log(this.studentPosition)
      for (let i = 0; i < this.studentPosition.length; i++) {
         marker = new AMap.Marker({
          // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]
          position: new AMap.LngLat(this.studentPosition[i].longitude, this.studentPosition[i].latitude)
        })
        this.map.add(marker)
       }
      }
      if (val === false) {
        // 初始化
        this.init()
      }
    },
    listenTrackSearchBool: function (val, oldVal) {
      if(val===true){
      this.$store.state.riskGradeBool =false
      this.$store.state.geoFenceBool =false
      this.$store.state.mapPosition =false
      }
    },
    listenRiskGradeBool: function (val, oldVal) {
      if(val===true){
      if(heatmap)
      {
        this.map.remove(heatmap)
      }
      let map = this.map
      let illPersonAddress = storage.get('illPersonAddress') || this.illPersonAddress
      // 构建热力图
      this.map.plugin(['AMap.Heatmap'], function () {
      // 初始化heatmap对象
        heatmap = new AMap.Heatmap(map, {
          radius: 30, // 给定半径
          opacity: [0, 0.8],
          gradient: {
            0.5: 'blue',
            0.65: 'rgb(117,211,248)',
            0.7: 'rgb(0, 255, 0)',
            0.9: '#ffea00',
            1.0: 'red'
          }
        })
        // 设置数据集：该数据为北京部分“郑州市部分病例”数据
        heatmap.setDataSet({
          data: illPersonAddress,
          max: 100
        })
      })
      heatmap.show()
      }
    },
    listenGeoFenceBool: function (val, oldVal) {
      if(val===true){
      this.$store.state.mapPosition =false
      this.$store.state.trackSearchBool=false
      this.$store.state.riskGradeBool =false
      this.geoFence.forEach((item,index)=>this.addGeoFence(item,index))
      }
      if (val === false) {
        this.init()
      }
    }
  }
}
</script>
<style scoped>
*{
  margin: 0;
  padding: 0;
}
#mapContainer{
  position:absolute;
  height: 90vh;
  width: 85%;
}
.mapMarkerCancel {
  height: 40px;
  width:100px;
  margin:0 10px;
  margin-top: 4px;
  position: absolute;
  border-radius: 30px;
  font-size: 10px;
  color: #FFF;
  text-decoration: none;
  /* 使用linear-gradient生成一个线性渐变的“背景图” */
  background-image: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
  /* 放大背景图 */
  background-size: 400%;
  /* 这里是为了让a标签的背景显示在a::before的下面 */
  z-index: 3;
}

.mapMarkerCancel:hover {
    /* 创建动画
        动画名称show
        执行时间8秒
        线性
        循环执行
    */
    color: #FFF;
    animation: show 8s linear infinite;
}

/* 动画主体
    从初始到结束，移动背景图的位置
*/

@keyframes show {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 400%;
    }
}

/* 创建背景炫光效果 */
.mapMarkerCancel::before{
    position: absolute;
    content: "";
    top:-5px;
    left:-5px;
    right:-5px;
    bottom:-5px;
    /* 使用linear-gradient生成一个线性渐变的“背景图” */
    background-image: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
    /* 放大背景图 */
    background-size: 400%;
    border-radius: 40px;
    z-index: -1;
    filter: blur(20px);
    /* 默认不显示 */
    opacity: 0;
}
.mapMarkerCancel:hover::before{
    opacity: 1;
    /* 背景炫光增加动画 */
    /* 与按钮的动画保持一致 */
    /* 创建动画
        动画名称show
        执行时间8秒
        线性
        循环执行
    */
    animation: show 8s linear infinite;
}
.searchInput{
  height: 50px;
  width: 500px;
  display: flex;
  align-content: center;
  justify-content: center;
}
.searchBtn{
  height: 40px;
  width:100px;
  margin-left: 20px;
  border-radius: 30px;
  background-color: #5800b0;
  border: none;
  color: white;
  padding: 2px 2px;
  margin: 10px 0;
  text-align: center;
  text-decoration: none;
  display:inline-block;
  font-size: 12px;
  margin: 4px 2px;
  cursor: pointer;
  -webkit-transition-duration: 0.4s;/* Safari */
  transition-duration:0.4s;
  border-radius: 30px;
}
.searchBtn:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
  background-color: #5800b0;
  }

</style>



