import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/login/login'
import map from '@/components/map'
import mainWindow from '@/components/MainWindow'
import home from '@/components/home'
import colockInManagement from '@/components/colockInManagement'
import leaveManagement from '@/components/leaveManagement'
import orderManagement from '@/components/orderManagement'
import leaveStuPopulation from '@/components/leaveStuPopulation'
import loading from '@/components/loading/loading.vue'
import search from '@/components/search.vue'
import nowNews from '@/components/nowNews.vue'
import notClockStu from '@/components/notClockStu.vue'
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/mainWindow',
      name: '首页',
      component: mainWindow,
      children: [
        {
          path: 'map',
          name: '地图显示',
          component: map,
          meta: {
            keepAlive: true
          }
        },
        {
          path: '',
          name: '首页',
          component: home
        },
        {
          path: 'colockInManagement',
          name: '打卡管理',
          component: colockInManagement
        },
        {
          path: 'leaveManagement',
          name: '请假管理',
          component: leaveManagement
        },
        {
          path: 'orderManagement',
          name: '预约管理',
          component: orderManagement
        },
        {
          path: 'leaveStuPopulation',
          name: '出校学生数',
          component: leaveStuPopulation
        },
        {
          path: 'search',
          name: '通知',
          component: search
        },
        {
          path: 'loading',
          name: 'loading',
          component: loading
        },
        {
          path: 'nowNews',
          name: '今日热点',
          component: nowNews
        },
        {
          path: 'notClockStu',
          name: '当前未打卡',
          component: notClockStu
        }
      ]
    }
  ]
})



