<template>
  <div class="container">
    <div class="card">
      <div class="imgBx">
        <a :href="this.News[0].url"><img :src="this.News[0].img" alt=""/></a>
      </div>
      <div class="content">
        <p>
          {{ this.News[0].img_message }}
        </p>
      </div>
    </div>
    <div class="card">
      <div class="imgBx">
        <a :href="this.News[1].url"><img :src="this.News[1].img" alt=""/></a>
      </div>
      <div class="content">
        <p>
          {{ this.News[1].img_message }}
        </p>
      </div>
    </div>
    <div class="card">
      <div class="imgBx">
        <a :href="this.News[2].url"><img :src="this.News[2].img" alt=""/></a>
      </div>
      <div class="content">
        <p>
          {{ this.News[2].img_message }}
        </p>
      </div>
    </div>
  </div>
</template>
<style scoped>
*{
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
}
.container{
    position: relative;
    height: 600px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 30px;
}

.container .card{
    margin-top: 100px;
    position: relative;
    max-width: 300px;
    height: 215px;
    background: #fff;
    margin: 30px 10px;
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 5px 202px rgba(0,0,0,0.5);
    transition: .3s ease-in-out;
}

.container .card:hover{
    height: 420px;
}

.container .card .imgBx{
    position: relative;
    width: 260px;
    height: 260px;
    top: -60px;
    left: 20px;
    z-index: 1;
    box-shadow: 0 5px 20px rgba(0,0,0,0.2);
}

.container .card .imgBx img{
    max-width: 100%;
    height: 100%;
    border-radius: 4px;
}

.container .card .content{
    position: relative;
    margin-top: -140px;
    padding: 10px 15px;
    text-align: center;
    color: #111;
    visibility: hidden;
    opacity: 0;
    transition: .3s ease-in-out;
    font-family: serif;
}

.container .card:hover .content{
    visibility: visible;
    opacity: 1;
    margin-top: -40px;
    transition-delay: .3s;
}
</style>
<script>
export default {
  data () {
    return {
      News: [{
        img: 'https://s3.ax1x.com/2020/11/30/D24GcQ.jpg',
        url: 'https://baijiahao.baidu.com/s?id=1684654938693664397&wfr=spider&for=pc',
        img_message: '中国有3000万男光棍？第七次“人普”将揭晓答案'
      },
      {
        img: 'https://s3.ax1x.com/2020/11/30/D2481g.jpg',
        url: 'https://baijiahao.baidu.com/s?id=1684613932487799183&wfr=spider&for=pc',
        img_message: '伊朗外长用中文发推：呼吁国际社会谴责国家恐怖主义 '
      },
      {
        img: 'https://s3.ax1x.com/2020/11/30/D24JXj.png',
        url: 'https://baijiahao.baidu.com/s?id=1684698757079128658&wfr=spider&for=pc',
        img_message: '华春莹连发三推为丁真“打call”！ '
      }
      ]
    }
  }
}
</script>



