<template>
  <div id="leaveStuChart"/>
</template>
<script>
import echarts from 'echarts'
// 实时数据数组
var time=[]
var stuNum=[];
function add0(m){
  return m<10?'0'+m:m
  }
export default {
  name:'LeaveStuPopulationChart',// 动态曲线图名称
  data() {
    return {
      // 动态曲线图初始化选项
      echartsOption:{
        legend:{
          data:['出校学生']
        },
        xAxis:{
          name:'时间',
          nameTextStyle:{
            fontWeight:600,
            fontSize:16
          },
          type:'category',
          splitLine: {
            show: false
          },
          data:time
        },
        yAxis:{
          name:'出校学生数',
          nameTextStyle:{
            fontWeight:600,
            fontSize:16
          },
          type:'value',
          scale:true,
          boundaryGap:[0, '100%'],
          splitLine: {
            show: false
          },
        },
        series:[
          {
            name:'出校学生',
            type:'line',
            emphasis:{
              label:{
                show:true,
                textStyle:{
                  color:'#333333',
                  fontWeight:'bold'
                }
              }
            },
          data: stuNum
          }
        ],
        tooltip: {
        trigger: 'axis',
        },
      }
    }
  },
  mounted(){
    this.initCharts()
    setInterval(this.addData,3000);// 3s更新一次数据到折线图
  },
  methods:{
    initCharts(){
       this.stuLeaveChart = echarts.init(document.getElementById('leaveStuChart'),'light')// 初始化echarts, theme为light
       this.stuLeaveChart.setOption(this.echartsOption)// 初始化选项}
    },
    updateChart(){
       this.stuLeaveChart.setOption(this.echartsOption)// 初始化选项
      console.log('更新')
     },
    // 将时间戳变成日期格式
    format(shijianchuo){
    // shijianchuo是整数，否则要parseInt转换
    var time = new Date(shijianchuo);
    var y = time.getFullYear();
    var m = time.getMonth()+1;
    var d = time.getDate();
    var h = time.getHours();
    var mm = time.getMinutes();
    var s = time.getSeconds();
    return y+'-'+add0(m)+'-'+add0(d)+' '+add0(h)+':'+add0(mm)+':'+add0(s);
    },
    addData:function(){
      let now=Date.parse(new Date())
      let randowNum=Math.floor(Math.random() * (0 - 8 + 1) + 8); // 获取随机数，范围0--8
      if(time.length>=7)
      {
        time.shift()
        stuNum.shift()
      }
      time.push(this.format(now))
      stuNum.push(randowNum)
      this.updateChart()
    }
  }
}
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  #leaveStuChart{
    width:85%;
    height:500px;
    margin: 0 auto;
  }

</style>



