<template>
  <div class="login-container">
    <el-form ref="form"
        :model="form"
        :rules="rules"
        label-width="80px"
        class="login-form">
      <h2 class="login-title">校园防疫管理平台</h2>
      <el-form-item label="用户名" prop='username'>
        <el-input v-model="form.username"/>
      </el-form-item>
      <el-form-item label="密码" prop='password'>
        <el-input v-model="form.password"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="loginButton" @click="submitForm('form')">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  data () {
    return {
      message: '',
      form: {
        username: '',
        password: ''
      },
      allowLogin: true,
      rules: {
        username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
        password: [{required: true, message: '请输入密码', trigger: 'blur'}]
      }
    }
  },
  watch: {
    'form.username': function (val, oldValue) {
      this.allowLogin = this.checkData()
    },
    'form.password': function (val, oldValue) {
      this.allowLogin = this.checkData()
    }
  },
  methods: {
    // 检校账户和密码是不是有效且存在的
    checkData () {
      if (this.form.username === 'admin' && this.form.password === 'admin') {
        return true
      }
        return false
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        //  验证通过为true，有一个不通过就是false console.log(valid)
        if (valid && this.allowLogin) {
          // valid = true
          console.log('1')
          this.$router.push('/mainWindow')
        } else {
          // valid = false
          alert('请输入正确的账户或密码')
          console.log('2')
        }
      })
    }
  }
}
</script>

<style acoped>
.login-form {
  width: 350px;
  margin: 160px auto; /* 上下间距160px，左右自动居中*/
  background-color: rgb(255, 255, 255, 0.8); /* 透明背景色 */
  padding: 30px;
  border-radius: 20px; /* 圆角 */
}
/* 背景 */
.login-container {
  position: absolute;
  width: 100%;
  height: 100%;
  background: url("../assets/14990.jpg");
}

/* 标题 */
.login-title {
  color: #303133;
  text-align: center;
  margin-left: 40px;
}
.loginButton {
    margin-left: 40px;
    position: relative;
    padding: 12px 40px;
    border-radius: 30px;
    font-size: 30px;
    color: #FFF;
    text-decoration: none;
    /* 使用linear-gradient生成一个线性渐变的“背景图” */
    background-image: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
    /* 放大背景图 */
    background-size: 400%;
    /* 这里是为了让a标签的背景显示在a::before的下面 */
    z-index: 1;
}
.loginButton:hover {
    /* 创建动画
        动画名称show
        执行时间8秒
        线性
        循环执行
    */
    animation: show 8s linear infinite;
}
/* 动画主体
    从初始到结束，移动背景图的位置
*/
@keyframes show {
    0% {
        background-position: 0%;
    }
    100% {
        background-position: 400%;
    }
}

/* 创建背景炫光效果 */
.loginButton::before{
    position: absolute;
    content: "";
    top:-5px;
    left:-5px;
    right:-5px;
    bottom:-5px;
    /* 使用linear-gradient生成一个线性渐变的“背景图” */
    background-image: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
    /* 放大背景图 */
    background-size: 400%;
    border-radius: 40px;
    z-index: -1;
    filter: blur(20px);
    /* 默认不显示 */
    opacity: 0;
}
.loginButton:hover::before{
    opacity: 1;
    /* 背景炫光增加动画 */
    /* 与按钮的动画保持一致 */
    /* 创建动画
        动画名称show
        执行时间8秒
        线性
        循环执行
    */
    animation: show 8s linear infinite;
}
</style>



