<template>
  <div class="container">
    <div class="card">
      <div class="imgBx">
        <a :href="this.News[0].url"><img :src="this.News[0].img" alt=""/></a>
      </div>
      <div class="content">
        <p>
          {{ this.News[0].img_message }}
        </p>
      </div>
    </div>
    <div class="card">
      <div class="imgBx">
        <a :href="this.News[1].url"><img :src="this.News[1].img" alt=""/></a>
      </div>
      <div class="content">
        <p>
          {{ this.News[1].img_message }}
        </p>
      </div>
    </div>
    <div class="card">
      <div class="imgBx">
        <a :href="this.News[2].url"><img :src="this.News[2].img" alt=""/></a>
      </div>
      <div class="content">
        <p>
          {{ this.News[2].img_message }}
        </p>
      </div>
    </div>
  </div>
</template>
<style scoped>
*{
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
}
.container{
    position: relative;
    height: 600px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 30px;
}

.container .card{
    margin-top: 100px;
    position: relative;
    max-width: 300px;
    height: 215px;
    background: #fff;
    margin: 30px 10px;
    padding: 20px 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 5px 202px rgba(0,0,0,0.5);
    transition: .3s ease-in-out;
}

.container .card:hover{
    height: 420px;
}

.container .card .imgBx{
    position: relative;
    width: 260px;
    height: 260px;
    top: -60px;
    left: 20px;
    z-index: 1;
    box-shadow: 0 5px 20px rgba(0,0,0,0.2);
}

.container .card .imgBx img{
    max-width: 100%;
    height: 100%;
    border-radius: 4px;
}

.container .card .content{
    position: relative;
    margin-top: -140px;
    padding: 10px 15px;
    text-align: center;
    color: #111;
    visibility: hidden;
    opacity: 0;
    transition: .3s ease-in-out;
}

.container .card:hover .content{
    visibility: visible;
    opacity: 1;
    margin-top: -40px;
    transition-delay: .3s;
}
</style>
<script>
export default {
  data () {
    return {
      News: [{
        img: 'http://p9.itc.cn/q_70/images03/20201108/ce01ff405c274453ad86b608531ff313.jpeg',
        url: 'https://www.sohu.com/a/430357124_612623?_trans_=000014_bdss_dkwhfy',
        img_message: '应对新冠肺炎疫情，全球公共卫生现三大短板 '
      },
      {
        img: 'http://p7.itc.cn/q_70/images03/20201108/36cb3c9d80eb4078980e14b6e53c3926.jpeg',
        url: 'https://www.sohu.com/a/430420023_114960?_trans_=000014_bdss_dkwhfy',
        img_message: '最新！欧美六国貂养殖场发现新冠病毒！'
      },
      {
        img: 'https://dss2.baidu.com/6ONYsjip0QIZ8tyhnq/it/u=3793413486,2619154171&fm=179&app=42&f=JPEG?w=320&h=160&s=FDDA779EF6324A235A57C5C6030030BF',
        url: 'https://www.sohu.com/a/430411590_162758?_trans_=000014_bdss_dkwhfy',
        img_message: '研究发现：儿童清除体内新冠病毒或比成年人快很多 '
      }
      ]
    }
  }
}
</script>



