<template>
  <el-container >
    <el-header class='indexHeader el-icon-date' >
      数据分析与管理平台
    </el-header>
    <el-menu class="headerMenu" mode="horizontal">
      <el-menu-item class="headerMenuBox" index="1" @click="handelHorizontalMenuClick">首页</el-menu-item>
      <el-menu-item class="headerMenuBox" index="2" @click="handelHorizontalMenuClick">日常管理</el-menu-item>
      <el-menu-item class="headerMenuBox" index="3" @click="handelHorizontalMenuClick">地图显示</el-menu-item>
      <el-menu-item class="headerMenuBox" index="4" @click="handelHorizontalMenuClick">数据展示</el-menu-item>
      <el-menu-item class="headerMenuBox" index="5" @click="handelHorizontalMenuClick">通知</el-menu-item>
    </el-menu>
    <el-container class="mainWindow_container">
      <div class="sideBar" >
        <el-aside v-show="this.$router.currentRoute.name==='地图显示'">
          <el-menu mode="vertical" class="sideMenu" >
            <el-menu-item class="sideMenuBox" index="1" @click="handelMenu_positionClick">显示位置</el-menu-item>
            <el-menu-item class="sideMenuBox" index="2" @click="handelMenu_trackClick">轨迹查询</el-menu-item>
            <el-menu-item class="sideMenuBox" index="4" @click="handelMenu_railClick">地理围栏</el-menu-item>
            <el-menu-item class="sideMenuBox" index="5" @click="handelMenu_rickClick">风险等级</el-menu-item>
          </el-menu>
        </el-aside>
        <el-aside v-show="this.$router.currentRoute.name==='打卡管理'||this.$router.currentRoute.name==='请假管理'||this.$router.currentRoute.name==='预约管理'">
          <el-menu mode="vertical" class="sideMenu" >
            <el-menu-item class="sideMenuBox" index="1" @click="handelMenu_colockClick">打卡管理</el-menu-item>
            <el-menu-item class="sideMenuBox" index="2" @click="handelMenu_leaveClick">请假管理</el-menu-item>
            <el-menu-item class="sideMenuBox" index="3" @click="handelMenu_orderClick">预约管理</el-menu-item>
          </el-menu>
        </el-aside>
        <el-aside v-show="this.$router.currentRoute.name==='首页'||this.$router.currentRoute.name==='今日热点'" >
          <el-menu mode="vertical" class="sideMenu" >
            <el-menu-item class="sideMenuBox" index="1" @click="handelMenu_nowNewsClick">今日热点</el-menu-item>
            <el-menu-item class="sideMenuBox" index="2" @click="handelMenu_illNewsClick">疫情新闻</el-menu-item>
          </el-menu>
        </el-aside>
        <el-aside v-show="this.$router.currentRoute.name==='出校学生数'||this.$router.currentRoute.name==='当前未打卡'">
          <el-menu mode="vertical" class="sideMenu" >
            <el-menu-item class="sideMenuBox" index="1" @click="handelMenu_leaveStuPopulationClick">出校学生实时数据</el-menu-item>
            <el-menu-item class="sideMenuBox" index="2" @click="handelMenu_notClockStuClick">未打卡学生实时数据</el-menu-item>
          </el-menu>
        </el-aside>
      </div>
      <el-main>
        <el-breadcrumb v-if="this.$router.currentRoute.path !== '/mainWindow'"
            separator-class="el-icon-arrow-right"
            class="breadCrumb">
          <el-breadcrumb-item :to = "{path : '/mainWindow'}">首页</el-breadcrumb-item>
          <el-breadcrumb-item>{{ this.$router.currentRoute.name }}</el-breadcrumb-item>
        </el-breadcrumb>
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive" />
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive" />
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
// import GLOBAL from '@/api/global_variable'
export default {
  data () {
    return {
      tabPosition: 'left'
    }
  },
  methods: {
    handelHorizontalMenuClick (e) {
      let index = e.index
      // console.log(index)
      if (index === '1') {
        this.$router.replace('/mainWindow')
      } else if (index === '2') {
        this.$router.replace('/mainWindow/colockInManagement')
      } else if (index === '3') {
        this.$router.replace('/mainWindow/map')
      } else if (index === '4') {
        this.$router.replace('/mainWindow/leaveStuPopulation')
      } else if (index === '5') {
        this.$router.replace('/mainWindow/search')
      }
    },
    handelMenu_positionClick (e) {
      // this.GLOBAL.BoolPosition = true
      this.$store.state.mapPosition = true
    },
    handelMenu_colockClick (e) {
      this.$router.replace('/mainWindow/colockInManagement')
    },
    handelMenu_leaveClick (e) {
      this.$router.replace('/mainWindow/leaveManagement')
    },
    handelMenu_orderClick (e) {
      this.$router.replace('/mainWindow/orderManagement')
    },
    handelMenu_trackClick (e) {
      this.$store.state.trackSearchBool = true
    },
    handelMenu_railClick (e) {
      this.$store.state.geoFenceBool = true
    },
    handelMenu_rickClick (e) {
      console.log('风险等级'+this.$store.state.riskGradeBool)
      this.$store.state.riskGradeBool = !this.$store.state.riskGradeBool
    },
    handelMenu_nowNewsClick (e) {
      this.$router.replace('/mainWindow/nowNews')
    },
    handelMenu_illNewsClick (e) {
      this.$router.replace('/mainWindow')
    },
    handelMenu_leaveStuPopulationClick (e) {
      this.$router.replace('/mainWindow/leaveStuPopulation')
    },
    handelMenu_notClockStuClick (e) {
      this.$router.replace('/mainWindow/notClockStu')
    },
    handelMenu_analyzeClick3 (e) {
      // this.$loading('正在加载中')
      // eslint-disable-next-line no-implied-eval
      // setTimeout('alert(\'分析完成\')', 2000)
    }
  },
  computed: {
    key () {
      return this.$route.path + Math.random()
    }
  }
}
</script>
<style scoped>
  .breadCrumb{
    margin-bottom: 20px;
  }
  .el-main {
    margin-top: 0px;
    width:100%;
  }
  .headerMenu{
    display: flex;
    flex:1;
    background: #333333;
    align-items: center;
    justify-content: center;
    color:white;
    width: 100%vw;
    justify-content: space-between;
    align-items: center;
  }
  .indexHeader{
    display: flex;
    background-color: #262f3e;
    top: 0;
    color: white;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding: 0 15px;
    box-sizing: border-box;
    margin-bottom: 0px;
  }
  .sideBar{
    line-height: 100vh;
    width:15%;
    margin: 0;
    padding: 0;
    color: white;
    overflow: auto;
    background: #333333;
  }
  .sideMenu{
    width:15%;
  }
  .el-menu-item{
    background: #333333;
    color:white;
  }
  .mainWindow_container{
    height: 100vh;
  }
  .sideMenuBox{
    width: 15vw;
    justify-content: space-between;
    align-items: center;
  }
  .el-menu--horizontal>.el-menu-item.is-active {
    border-bottom: 2px solid #409EFF;
    color: white;
    margin:0;
  }
  .aside{
    width: 15%;
  }
  .headerMenuBox {
    position: relative;
    color: #FFF;
    text-decoration: none;
    padding: 6px 18px;
    /* background-color: #3ce745; */
    transition: background-color 0.8s;
}

.headerMenuBox:hover {
    background-color: #e74c3c;
}

.headerMenuBox::before {
    position: absolute;
    content: "";
    width: 18px;
    height: 18px;
    border: 2px solid #e74c3c;
    border-width: 0 0 2px 2px;
    left: 0;
    bottom: 0;
    opacity: 0;
    transition: 0.2s;
}

.headerMenuBox::after {
    position: absolute;
    content: "";
    width: 18px;
    height: 18px;
    border: 2px solid #e74c3c;
    border-width: 2px 2px 0 0;
    top: 0;
    right: 0;
    opacity: 0;
    transition: 0.2s;
}

.headerMenuBox:hover::before {
    left: -12px;
    bottom: -12px;
    opacity: 1;
}

.headerMenuBox:hover::after {
    top: -12px;
    right: -12px;
    opacity: 1;
}
</style>



