import Vue from 'vue'
import App from './App'
import router from './router/index'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueMap from '@amap/amap-vue'
import Vuex from 'vuex'
import globalVariable from '@/api/global_variable.js'
import JsonExcel from 'vue-json-excel'
import LoadingSpin from 'vue-loading-spin'
import 'vue-loading-spin/dist/loading.css'
Vue.use(LoadingSpin, {
    // 是否固定位置，默认为true
    isFixed: true,
    // 是否自动注册为全局组件，如果设置为false就不会注册成组件来使用
    isComponent: true,
    // 是否显示动画效果，默认为true
	isShowAnimation: true
})
Vue.component('downloadExcel', JsonExcel)
Vue.prototype.GLOBAL = globalVariable
Vue.use(ElementUI)
Vue.use(VueMap)
Vue.use(Vuex)
var store = new Vuex.Store({
  state: {
    trackSearchBool: false,
    mapPosition: false,
    riskGradeBool: false,
    geoFenceBool: false
  }
})
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})
export default store



